// Завадання 1
let count = 0;
let interval = setInterval(() => {
  count++;
  alert("Через 5с буде повідомлення " + count);
  if (count === 5) {
    clearInterval(interval);
  }
}, 5000); 
// Завдання 2
let boxes = document.querySelectorAll('.box');
let position = 0;

setInterval(() => {
  position += 5;
  boxes.forEach((box, index) => {
    box.style.left = (position + index * 20) + 'px';
    box.style.width = (50 + Math.sin(position / 10 + index) * 10) + 'px';
  });
}, 100);